	module.exports = {"data":{"wordpressPost":{"title":"Medical writing support, postnuclear disaster evacuation, and publication culture: Most read articles in February","content":"<h1>Medical writing support and the quality of reporting, postnuclear disaster evacuation, and the current publication culture</h1>\n<p><img class=\"alignnone\" title=\"Image: Left-handed writing with wristwatch.jpg, from Wikimedia Commons, by Alejandro Escamilla (Unsplash) \" src=\"https://upload.wikimedia.org/wikipedia/commons/thumb/0/02/Left-handed_writing_with_wristwatch.jpg/800px-Left-handed_writing_with_wristwatch.jpg\" alt=\"File:Left-handed writing with wristwatch.jpg\" width=\"372\" height=\"248\" /></p>\n<p>Once again, this month sees a selection of new papers entering the Top 10 Most Read list, alongside some old favourites. Returning to the top spot this month is Teschke<em> et al</em>‘s paper on bicycling injury hospitalisation rates, which continues to attract attention and generate discussion via our eLetters section. Entering at number two is a cross-sectional study examining the associations between mortality and registered nurse staffing in English hospital trusts, complete with podcast and press release. Gaining a significant amount of attention on Twitter, Gattrell <em>et al</em> examine the relationship between medical writing support and the quality of reporting of the results of randomised controlled trials at number three, and at number eight Nomura <em>et al</em> write about the long-term impact of postnuclear disaster evacuation on diabetes, hyperlipidaemia and hypertension. Concluding the list at number ten, Tijdink <em>et al</em> investigate the biomedical scientist&#8217;s perception of the prevailing publication culture.</p>\n<table border=\"1\" width=\"483\" cellspacing=\"0\" cellpadding=\"0\">\n<tbody>\n<tr>\n<td valign=\"top\" width=\"50\">Rank</td>\n<td valign=\"top\" width=\"132\">Author(s)</td>\n<td valign=\"top\" width=\"301\">Title</td>\n</tr>\n<tr>\n<td width=\"50\">1</td>\n<td width=\"132\">Teschke <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/11/e008052.full\">Bicycling injury hospitalisation rates in Canadian jurisdictions: analyses examining associations with helmet legislation and mode share</a></td>\n</tr>\n<tr>\n<td width=\"50\">2</td>\n<td width=\"132\">Griffiths <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/2/e008751.full\">Registered nurse, healthcare support worker, medical staffing levels and mortality in English hospital trusts: a cross-sectional study</a></td>\n</tr>\n<tr>\n<td width=\"50\">3</td>\n<td width=\"132\">Gattrell<em> et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/2/e010329.full\">Professional medical writing support and the quality of randomised controlled trial reporting: a cross-sectional study</a></td>\n</tr>\n<tr>\n<td width=\"50\">4</td>\n<td width=\"132\">Steffens <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/2/e010164.full\">Social group memberships in retirement are associated with reduced risk of premature death: evidence from a longitudinal cohort study</a></td>\n</tr>\n<tr>\n<td width=\"50\">5</td>\n<td width=\"132\">Pitman <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/1/e009948.full\">Bereavement by suicide as a risk factor for suicide attempt: a cross-sectional national UK-wide study of 3432 young bereaved adults</a></td>\n</tr>\n<tr>\n<td width=\"50\">6</td>\n<td width=\"132\">Marsh <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/1/e009949.full\">Cost-effectiveness analysis of arthroscopic surgery compared with non-operative management for osteoarthritis of the knee</a></td>\n</tr>\n<tr>\n<td width=\"50\">7</td>\n<td width=\"132\">Greenhalgh <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/1/e009388.full\">Virtual online consultations: advantages and limitations (VOCAL) study</a></td>\n</tr>\n<tr>\n<td width=\"50\">8</td>\n<td width=\"132\">Nomura <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\">P<a href=\"http://bmjopen.bmj.com/content/6/2/e010080.full\">ostnuclear disaster evacuation and chronic health in adults in Fukushima, Japan: a long-term retrospective analysis</a></td>\n</tr>\n<tr>\n<td width=\"50\">9</td>\n<td width=\"132\">Kristensen<em> et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/9/e007118.full\">The effect of statins on average survival in randomised trials, an analysis of end point postponement</a></td>\n</tr>\n<tr>\n<td width=\"50\">10</td>\n<td width=\"132\">Tijdink <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/2/e008681.full\">How do scientists perceive the current publication culture? A qualitative focus group interview study among Dutch biomedical researchers</a></td>\n</tr>\n</tbody>\n</table>\n<p><em>Most read figures are based on pdf downloads and full text views. Abstract views are excluded.</em><!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"5bc4345a-699d-551c-8986-b1ccfd5b1299"}}

